body, *, ::before, ::after {

  margin: 0;
  padding: 0;
  box-sizing: border-box;

  font-family: Calibri, sans-serif;

}


.App {
  width: 100vw;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App > * {

  margin: 25px;

}

h2 {

  font-size: 40px;
  letter-spacing: 10px;
  color: rgba(0, 0, 0, 0.534);
}

.grille {

  width: 500px;
  height: 500px;
  background-color: rgb(71, 159, 231);

  display: flex;
  flex-direction: column;

}

.colonne {

  flex: 33%;
  display: flex;
  flex-direction: row;

}

.square {

  flex: 33%;
  height: 100%;
  border: 1px solid rgb(177, 185, 219);
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 45px;
  font-weight: bold;
  color: #f1f1f1;
}

.square:active {

  background-color: rgba(21, 135, 228, 0.507);

}
.square:selected {

  background-color: grey;

}

button {

  width: auto;
  height: auto;

  padding: 10px;
  border-radius: 10px;
  border: none;

  font-size: 20px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);

  cursor: pointer;

  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.534),
              -0.5px -0.5px 2px rgba(0, 0, 0, 0.1);

              transition: all 0.2s;

}

button:hover { color: #f1f1f1; background-color: rgb(57, 142, 212); }

.test {

  width: 500px;
  height: 500px;
  background-color: red;
}